import { format, isValid, parseISO } from 'date-fns';
import { Injectable } from '@angular/core';
import { MyDateConverter } from './myDateConverter';

@Injectable({
    providedIn: 'root'
})
export class DateTimeHelperService {

    private $D = null;

    constructor() {
        this.$D = new MyDateConverter();
    }
    /**
     * @param value è¦è½¬æ¢æ ¼å¼çæ¥æ
     * @param fmt æ ¼å¼åå­ç¬¦ä¸²
     *
     * æ´å¤çæ ¼å¼åè¯·åè
     * [ç¹æç¹æ](https://date-fns.org/v1.30.1/docs/format)
     */
    formatTo(value: any, fmt: string = 'yyyy-MM-dd') {
        if (!value) {
            return '';
        }

        if (typeof value === 'string' && value.indexOf('0001') === 0) {
            return '';
        }

        if (value instanceof Date) {
            return format(value, fmt);
        }

        let _d: any = parseISO(value);

        if (_d == 'Invalid Date') {
            _d = this.createDate(value) || new Date(value);
        }

        // const d = parseISO(value);
        if (isValid(_d)) {
            const d = parseISO(format(_d, 'yyyy-MM-dd HH:mm:ss'));
            return format(d, fmt);
        } else {
            // console.error(`${value} è½¬æ¢ä¸ºDateæ¶å¤±è´¥ã`);
            if (fmt.indexOf('HH') === 0 || fmt.indexOf('hh') === 0) {
                // ä»ææ¶é´é¨å
                // æåæ¶é´
                const _time = value.match(/\d*/g).filter(n => n!== '').join(':');
                const fullDateTime = new Date('2022-11-25 ' + _time);
                return format(fullDateTime, fmt);
            }
            return '';
        }
    }

    private createDate(val)     {
        return this.$D.create(val);
    }

}
